<template>
  <!-- The Modal -->
  <div id="modalBus" class="modal">
    <div class="modal-dialog modal-xl">
      <div class="modal-content">
        <!-- Modal Header -->
        <div class="modal-header">
          <h4 class="modal-title">Horários</h4>
          <button type="button" class="close" data-dismiss="modal">
            &times;
          </button>
        </div>

        <template>
          <div>
            <b-alert
              :show="dismissCountDown"
              dismissible
              variant="success"
              @dismissed="dismissCountDown = 0"
              @dismiss-count-down="countDownChanged"
            >
              Horários atualizados
            </b-alert>
          </div>
        </template>

        <!-- Modal body -->
        <div class="modal-body">
          <form action>
            <div class="row">
              <div class="col">
                <div class="input-group mb-3">
                  <select
                    class="form-control"
                    @change="select(selected)"
                    v-model="selected"
                  >
                    <option value disabled selected>Selecionar Rota</option>
                    <option
                      v-for="(value, index) in lines"
                      :key="index"
                      :value="value.id"
                    >
                      {{ value.number }} - {{ value.description }} -
                      {{ value.sense }}
                    </option>
                  </select>
                </div>
              </div>
            </div>
            <div
              style="
                position: relative;
                overflow-y: auto;
                overflow-x: hidden;
                max-height: 500px;
              "
            >
              <div class="row">
                <div class="col">
                  <b-table
                    v-show="showTable"
                    :items="table[`${radio}`]"
                    :fields="fields"
                    :busy="isBusy"
                    small
                    hover
                    borderless
                    responsive
                    striped
                    class="text-center rounded-lg shadow-lg"
                  >
                    <template v-slot:thead-top>
                      <b-tr>
                        <b-th colspan="3">
                          <b-form-group label="Programação">
                            <b-form-radio-group
                              size="sm"
                              v-model="radio"
                              name="radios-btn"
                              buttons
                              button-variant="outline-secondary"
                            >
                              <b-form-radio value="1">{{
                                options[0].text
                              }}</b-form-radio>
                              <b-form-radio value="6">{{
                                options[1].text
                              }}</b-form-radio>
                              <b-form-radio value="0">{{
                                options[2].text
                              }}</b-form-radio>
                              <b-form-radio value="7">
                                {{ options[3].text }}
                                <b-form-checkbox
                                  id="holidayx"
                                  v-model="holidayx"
                                  name="holidayx"
                                  value="true"
                                  unchecked-value="false"
                                >
                                </b-form-checkbox>
                              </b-form-radio>
                              <b-form-radio value="8">
                                {{ options[4].text }}
                                <b-form-checkbox
                                  id="checkbox"
                                  v-model="holiday"
                                  name="checkbox"
                                  value="true"
                                  unchecked-value="false"
                                >
                                </b-form-checkbox>
                              </b-form-radio>
                            </b-form-radio-group>
                          </b-form-group>
                        </b-th>
                        <b-th colspan="3">
                          <b-form-group label="Hora e Ônibus">
                            <b-button-group size="sm">
                              <b-button variant="primary" @click="show = !show">
                                <b-icon-plus-square></b-icon-plus-square>
                              </b-button>
                              <b-button variant="success" @click="reply">
                                <b-icon-reply v-if="!replyTable"></b-icon-reply>
                                <b-icon-reply-all-fill
                                  v-if="replyTable"
                                ></b-icon-reply-all-fill>
                              </b-button>
                            </b-button-group>
                          </b-form-group>
                        </b-th>
                      </b-tr>

                      <!-- ADICIONAR HORÁRIO -->
                      <b-tr v-show="show">
                        <b-th colspan="12">
                          <b-input-group size="sm" prepend="Saída">
                            <b-form-input
                              type="time"
                              id="startTime"
                              name="startTime"
                              step="2"
                              v-model="form.start"
                              style="max-width: 110px"
                            ></b-form-input>
                            <b-input-group-prepend is-text
                              >Chegada</b-input-group-prepend
                            >
                            <b-form-input
                              type="time"
                              id="endTime"
                              name="endTime"
                              step="2"
                              v-model="form.end"
                              style="max-width: 110px"
                            ></b-form-input>
                            <b-input-group-prepend is-text
                              >Ônibus</b-input-group-prepend
                            >
                            <b-form-select
                              v-model="form.id"
                              @change="findBus(form.id)"
