      this.getBus();
      this.isBusy = !this.isBusy;
      let data = {
        holiday: this.holiday,
        holidayx: this.holidayx,
        weekdays: {},
      };
      data.weekdays = this.table;
      this.$http
        .put(`schedules/${this.schedule_id}`, {
          hour: data,
        })
        .then(() => {
          this.getBus();
          this.isBusy = !this.isBusy;
          this.showAlert();
          // barramento.$emit("edited", "Dados de horários salvos!");
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    validBus(data) {
      // console.log(data);
      return (
        this.table[`${this.radio}`].filter(
          (item) =>
            item.id == data.id &&
            item.start == data.start &&
            item.end == data.end
        ).length > 0
      );
    },
    reply() {
      if (this.radioTemp) {
        this.replyTable = true;
        this.radioTemp = false;
        let table = JSON.parse(JSON.stringify(this.table[`${this.radio}`]));
        this.replyTemp = table;
        alert("Dados copiados!");
      } else {
        this.table[`${this.radio}`] = this.replyTemp;
        let table = JSON.parse(JSON.stringify(this.table));
        this.table = table;
        // console.log(this.table);
        this.replyTable = false;
        this.radioTemp = true;
        alert("Dados inseridos!");
      }
    },
    filterBus(id) {
      return this.bus.filter((item) => item.id === id);
    },
  },
  watch: {
    table(v1) {
      this.temp = JSON.parse(JSON.stringify(v1));
    },
  },
};
</script>